package com.jine.zouwedding.ui.views

import androidx.compose.foundation.layout.*
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.material3.Button
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Text
import androidx.compose.material3.TextButton
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp

@Composable
fun VerticalSpacing(size: Dp) {
    Spacer(modifier = Modifier.height(size))
}

@Composable
fun ColumnScope.VerticalSpacing(weight: Float) {
    Spacer(modifier = Modifier.weight(weight))
}

@Composable
fun HorizontalSpacing(size: Dp) {
    Spacer(modifier = Modifier.width(size))
}

@Composable
fun RowScope.HorizontalSpacing(weight: Float) {
    Spacer(modifier = Modifier.weight(weight))
}

@Composable
fun SectionTitle(title: String) {
    Text(
        text = title,
        style = MaterialTheme.typography.headlineMedium,
        fontWeight = FontWeight.Bold,
    )
}

@Composable
fun Body(modifier: Modifier = Modifier, content: String, fontWeight: FontWeight = FontWeight.Normal) {
    Text(
        modifier = modifier,
        text = content,
        style = MaterialTheme.typography.bodyLarge,
        fontWeight = fontWeight
    )
}

@Composable
fun WeddingButton(label: String, onClick: () -> Unit) {
    Button(onClick = onClick, shape = RoundedCornerShape(30.dp)) {
        Text(label)
    }
}

@Composable
fun TopAppBarDefaultButton(label: String, onClick: () -> Unit) {
    TextButton(onClick = onClick) {
        Text(label)
    }
}

@Composable
fun TopAppBarPrimaryButton(label: String, onClick: () -> Unit) {
    WeddingButton(label = label, onClick = onClick)
}

fun Modifier.horizontalDefaultPadding(): Modifier = this.then(padding(horizontal = 256.dp))