package com.jine.zouwedding

import com.jine.zouwedding.data.UserModel
import io.ktor.client.*
import io.ktor.client.request.*
import io.ktor.client.statement.*
import io.ktor.http.*
import io.ktor.utils.io.core.*
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.launch

class WeddingViewModel : ZouViewModel() {

    private val _choiceUpdateNotification = MutableStateFlow<ChoiceUpdateNotification?>(null)
    val choiceUpdateNotification: StateFlow<ChoiceUpdateNotification?> get() = _choiceUpdateNotification.asStateFlow()

    fun notificationHasBeenConsumed() {
        _choiceUpdateNotification.value = null
    }

    fun updateGuestChoice(user: UserModel) {
        _choiceUpdateNotification.value = null

        viewModelScope.launch {
            createHttpClient().use {
                val response = makeRequest(it, user.copy(hasAnswered = true))
                if (response.status.value in 200..299) {
                    _choiceUpdateNotification.value = ChoiceUpdateNotification.Success
                } else {
                    _choiceUpdateNotification.value = ChoiceUpdateNotification.Error
                }
            }
        }
    }

    private suspend fun makeRequest(
        httpClient: HttpClient,
        user: UserModel
    ): HttpResponse {
        return httpClient.request("https://api.baserow.io/api/database/rows/table/302843/${user.id}/") {
            method = HttpMethod.Patch
            headers {
                append("Authorization", "Token o0pJ9uzhCRFeCHA3KKCpWAiTw68oAR8C")
            }
            url {
                parameters.append("user_field_names", "true")
            }
            contentType(ContentType.Application.Json)
            setBody(user)
        }
    }

    sealed interface ChoiceUpdateNotification {
        object Success : ChoiceUpdateNotification
        object Error : ChoiceUpdateNotification
    }
}