package com.jine.zouwedding.ui.sections

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.*
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Text
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.alpha
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp
import com.jine.zouwedding.data.UserModel
import com.jine.zouwedding.ui.views.Body
import com.jine.zouwedding.ui.views.VerticalSpacing

@Composable
fun Planning(height: Dp, user: UserModel) {
    Section(height = height, title = "C'est quoi le planning ?", customContent = {
        Row(
            modifier = Modifier.fillMaxWidth().padding(top = 64.dp),
            horizontalArrangement = Arrangement.Center
        ) {
            Spacer(modifier = Modifier.weight(0.5f).background(Color.Red))
            if (user.isInvitedFriday) {
                PlanningBlock(
                    zone = "Mairie",
                    dateString = "Vendredi 14 fvrier",
                    hourString = "16h00",
                    address = "L'adresse, blababla"
                )
                Spacer(modifier = Modifier.weight(1f).background(Color.Blue))
            }
            if (user.isInvitedFullSaturday) {
                PlanningBlock(
                    zone = "Crmonie, Cocktail & Soire",
                    dateString = "Samedi 15 fvrier",
                    hourString = "16h00",
                    address = "L'adresse, blababla"
                )
            } else {
                PlanningBlock(
                    zone = "Cocktail",
                    dateString = "Samedi 15 fvrier",
                    hourString = "18h00",
                    address = "L'adresse, blababla"
                )
            }
            if (user.isInvitedSunday) {
                Spacer(modifier = Modifier.weight(1f).background(Color.Green))
                PlanningBlock(
                    zone = "Brunch",
                    dateString = "Dimanche 16 fvrier",
                    hourString = "12h00",
                    address = "L'adresse, blababla"
                )
            }
            Spacer(modifier = Modifier.weight(0.5f).background(Color.Magenta))
        }
    })
}

@Composable
private fun PlanningBlock(zone: String, dateString: String, hourString: String, address: String) {
    Column(horizontalAlignment = Alignment.CenterHorizontally) {
        Box(modifier = Modifier.size(60.dp).background(color = Color.Red))
        VerticalSpacing(8.dp)
        Body(content = zone, fontWeight = FontWeight.Bold)
        VerticalSpacing(8.dp)
        Body(content = dateString)
        Body(content = hourString, modifier = Modifier.alpha(0.66f))
        VerticalSpacing(8.dp)
        Text(address, fontWeight = FontWeight.Normal, style = MaterialTheme.typography.bodySmall)
    }
}