package com.jine.zouwedding.ui.sections

import androidx.compose.animation.core.*
import androidx.compose.foundation.Image
import androidx.compose.foundation.clickable
import androidx.compose.foundation.interaction.MutableInteractionSource
import androidx.compose.foundation.layout.*
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Text
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.remember
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.ColorFilter
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp
import com.jine.zouwedding.data.UserModel
import com.jine.zouwedding.ui.views.VerticalSpacing
import com.jine.zouwedding.ui.views.WeddingButton
import compose.icons.FeatherIcons
import compose.icons.feathericons.ArrowDown


@Composable
fun Header(height: Dp, user: UserModel, onRSVPButtonClicked: () -> Unit, onScrollClicked: () -> Unit) {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .height(height),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        Spacer(modifier = Modifier.weight(1f))
        Text(
            text = "Bienvenue ${user.firstname} ! Tu es invit au",
            style = MaterialTheme.typography.titleLarge,
            fontWeight = FontWeight.Normal
        )
        VerticalSpacing(32.dp)
        Text(
            text = "Mariage des Zous",
            style = MaterialTheme.typography.displayLarge,
            fontWeight = FontWeight.Black
        )
        VerticalSpacing(32.dp)
        Text(
            text = getDateString(user),
            style = MaterialTheme.typography.titleLarge,
            fontWeight = FontWeight.Normal
        )
        VerticalSpacing(32.dp)
        WeddingButton("Je viens !") { onRSVPButtonClicked() }
        Column(
            modifier = Modifier.weight(1f).padding(bottom = 32.dp),
            verticalArrangement = Arrangement.Bottom,
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Text("Tu veux tout savoir sur ce mariage ? a se passe en dessous !")
            val position by rememberInfiniteTransition().animateFloat(
                initialValue = -3f, targetValue = 3f, animationSpec = infiniteRepeatable(
                    tween(300), RepeatMode.Reverse
                )
            )
            VerticalSpacing(12.dp)
            val interactionSource = remember { MutableInteractionSource() }
            Image(
                imageVector = FeatherIcons.ArrowDown,
                contentDescription = "ScrollDown",
                colorFilter = ColorFilter.tint(MaterialTheme.colorScheme.onBackground),
                modifier = Modifier.size(32.dp).offset(y = position.dp)
                    .clickable(interactionSource = interactionSource, indication = null) { onScrollClicked.invoke() }
            )
        }
    }
}

private fun getDateString(user: UserModel): String {
    var dateString = ""
    if (user.isInvitedFriday) {
        if(user.isInvitedSunday) {
            dateString += "14, "
        } else {
            dateString += "14 & "
        }
    }
    dateString += "15"
    if (user.isInvitedSunday) {
        dateString += " & 16"
    }
    dateString += " fvrier 2025"
    return dateString
}