package com.jine.zouwedding.data

enum class FoodModel {
    DEFAULT {
        override fun getLabel(): String = "Burger classique"
    },
    CHEESE {
        override fun getLabel(): String = "Burger au fromage"
    },
    VEGE {
        override fun getLabel(): String = "Burger vgtarien"
    };

    abstract fun getLabel(): String

    companion object {
        fun findFrom(string: String?) = if(string.isNullOrBlank()) null else FoodModel.valueOf(string)
        fun getAll() = listOf(DEFAULT, CHEESE, VEGE)
    }
}